body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: white;   /* ü§ç Full white background */
  color: #000;               /* üñ§ Text should be black */
}

.App {
  background-color: white;
  min-height: 100vh;
  padding: 20px;
}

input, button {
  font-size: 16px;
  border-radius: 4px;
  border: none;
  outline: none;
}

input {
  padding: 8px;
  border: 1px solid #ccc;
  background-color: #fff;
  color: #000;
}

button {
  background-color: #ff0000;
  color: white;
  cursor: pointer;
  padding: 8px 16px;
  font-weight: 500;
}

button:hover {
  background-color: #cc0000;
}
